:root {
  --primary: #44b07b;
  --danger: red;
  --black: #111111;
  --dark-grey: rgba(97, 93, 93, 0.5);
  --grey: #808080;
  --light-grey: #999999;
  --blur-white: #bdb4b4;
  --lightest-grey: #e9e9e9;
  --white: #fff;
  --font-size: 13px;
  --font-style: normal;
  --font-variant: normal;
  --letter-spacing: normal;
  --font-family-gilroySemi: 'Gilroy-SemiBold';
  --font-family-avenirmedium: AvenirMedium;
  --font-family-bigCaslon: 'BigCaslon-Medium';
  --z-index-highest: 10002;
}

/* global */
.sv-body__page {
  margin: 0px 0px 60px 0px;
}

.sv-root-modern {
  margin: 0 auto;
  text-align: left;
}

/* elements */
.sv-header__text {
  text-align: center;
}
.sv-header__text span {
  font-family: var(--font-family-bigCaslon);
  font-size: 44px;
  font-weight: 400;
  color: var(--black);
  margin-top: 0px;
  margin-bottom: 30px;
}

/* main title */
.sv-root-modern .sv-container-modern__title {
  text-align: center;
  padding-top: 0;
  padding-bottom: 0;
  margin-bottom: 30px;
}

.sv-root-modern .sv-container-modern__title h3 {
  font-family: var(--font-family-bigCaslon);
  font-size: 44px;
  font-weight: 400;
  line-height: 1.4;
}

.sv-page__title {
  font-family: var(--font-family-bigCaslon);
  font-size: 44px;
  font-weight: 400;
  color: var(--black);
  padding-left: 0;
  margin-top: 0px;
  margin-bottom: 20px;
}

.sv-root-modern .sv-page__title + .sv-page__description {
  color: var(--grey);
  min-height: 0;
  padding-left: 0;
  margin: 0 0 20px;
}

.sv-form-control {
  font-family: var(--font-family-avenirmedium);
  color: var(--black);
  font-size: 13px;
  font-style: normal;
  font-variant: normal;
  letter-spacing: normal;
  margin-bottom: 20px;
}
.sv-title-text {
  font-weight: normal;
  font-size: 13px;
  width: 100%;
  margin-bottom: 15px;
  color: var(--black);
  margin: 0 0 15px;
}
.sv-selectbase__item {
  margin-bottom: 1em;
  vertical-align: middle;
}
.sv-selectbase__item .sv-selectbase__label {
  display: flex;
  align-items: center;
}
h5 {
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0;
  margin-inline-end: 0;
}
.sv-panel .sv-row:not(:last-child) {
  padding-bottom: 0;
  margin-bottom: 0;
}
.sv-row:not(:last-child) {
  padding-bottom: 0;
}
.sv-panel__title {
  color: white;
  font-family: var(--font-family-gilroySemi);
  font-size: 16px;
  text-indent: 10px;
  padding-top: 13px;
  padding-bottom: 13px;
  background: var(--primary);
}
.sv-item__control-label {
  padding-top: 0;
  width: 100%;
}
body .sv-root-modern ::-webkit-scrollbar-thumb {
  background: var(--primary);
}
.sv-question__required-text,
.sv-question__num {
  display: none;
}
.sv-table__cell {
  min-width: 18.5em;
}
.sv-table .sv-radio-group-wrapper .sv-radio-group-item,
.sv-table .sv-checkbox-wrapper .sv-checkbox-group-item {
  flex-basis: unset;
  margin-bottom: 0.8em;
}
.header-cell {
  text-align: center;
}
.container .sv-root-modern .sv-file__choose-btn {
  background-color: var(--primary);
}
.sv-question {
  overflow: hidden;
}
.sv-question__content {
  margin-left: 0;
}
.sv-form-control .sv-question__description {
  margin: 0 0 15px;
  padding-left: unset;
  font-size: 13px;
  word-break: break-word;
}
.sv-root-modern .sv-form-control .sv-question__description {
  color: var(--grey);
}

.sv-row {
  flex-wrap: unset;
}
.sv-root-modern .sv-body__footer {
  margin: 0 auto 60px;
  padding: 0;
  width: auto;
  display: flex;
  flex-direction: row-reverse;
}
.sv-root-modern .sv-footer .sv-btn {
  display: inline-block;
  padding: 12px 0;
  color: var(--black);
  border: 2px solid var(--black);
  font-family: var(--font-family-gilroySemi);
  font-size: 13px;
  text-align: center;
  font-weight: normal;
  padding: 12px 36px;
}

/* 3rd party widgets */
.noUi-base .noUi-connect {
  background: var(--primary);
  width: unset;
}
.noUi-value {
  margin-top: 6px;
  font-size: 13px;
}
.noUi-horizontal {
  margin: 0 30px;
}
.noUi-tooltip {
  font-size: 13px;
}

/* progress bar */
.sv_progress-buttons__container-center {
  position: sticky;
  top: 110px;
  background-color: var(--white);
  padding-top: 26px;
  padding-bottom: 30px;
  z-index: var(--z-index-highest);
}


